.fullpage-wrapper {
    position: absolute !important;
    top: 0;
    left: 0;
    width: 100%;
    height: 100% !important;
    overflow: hidden;
    z-index: 100;
    transform: none !important;
    min-height: 100% !important; }

.section {
    position: absolute !important;
    top: 0;
    left: 0;
    width: 100%;
    height: 100% !important;
    opacity: 0;
    visibility: hidden;
    z-index: 1;
    transition: opacity 0.3s, visibility 0.3s, z-index 0.3s;
    overflow: auto;
    &.active {
        opacity: 1;
        visibility: visible;
        z-index: 1; }
    .fp-tableCell {
        padding-top: 90px;
        padding-bottom: 60px; }
    @include sm {
        opacity: 1;
        visibility: visible;
        position: relative !important; } }

@include sm {
    #fullpage {
        .section {
            .feature-list,
            .title,
            .fp-text,
            .about_results {
                display: none; } }
        .section {
            .image {
                display: block; }
            .project_link {
                display: block; } }
        .wrapper {
            min-height: inherit; }
        [class$="vh"] {
            margin-bottom: 0;
            margin-top: 0; } } }



.wrapper {
    width: 100%;
    font-size: 20px;
    position: relative;
    text-align: left;
    min-height: 570px;
    @include css-lock-height(14,20,550,700);
    img {
        display: block;
        @include rh(700) {
            width: calc(75% + (100 - 75) * ((100vh - 600px) / (700 - 600))); } } }

.titleLarge {
    font-size: 30px;
    line-height: 1.5; }

.project_link {
    font-size: 20px;
    line-height: 30px;
    font-weight: 400; }

.project_link {
    display: block;
    &__arrow {
        visibility: hidden;
        opacity: 0;
        transition: 0.8s opacity, 0.8s visibility; }
    &:hover .project_link__arrow {
        visibility: visible;
        opacity: 1;
        transition: 0.8s opacity, 0.8s visibility; } }


