.about {
    margin-top: 156px;
    margin-bottom: 90px;

    &__title h3 {
        margin-bottom: 20px;
        font-size: 20px;
        line-height: 20px;
        font-weight: 400; }
    &__list {
        line-height: 28px; }
    &__text {
        line-height: 28px;
        &--wide {
            line-height: 30px; } }
    &__list--muted {
        margin-top: 40px;
        color: $light-grey; }
    &__contacts {
        margin-bottom: 48px;
        @include sm {
            display: none; } } }

.static {
    .footer {
        position: static; } }

