#slider {
    background-color: $text-dark;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: hidden; }

.slider-title {
    font-size: 20px;
    color: $orange;
    text-decoration: underline; }

.slider {
    position: relative;
    width: 2102px;
    margin: 0 -65px 0 -65px;
    opacity: 0;
    visibility: hidden;
    transition: opacity 2s, visibility 2s;
    @include sm {
        width: auto;
        margin: 0; }
    &-wrapper {
        margin-top: 200px; }
    &:before {
        position: absolute;
        top: 0;
        bottom: 0;
        right: calc(100% - 73px);
        background-color: $text-dark;
        z-index: 100;
        content: '';
        width: 20%;
        @include sm {
            display: none; } } }





.slide {
    transition: transform 0.8s;
    .slick-current & {
        transform: translateY(42px); }

    &__img {
        padding-bottom: 410px;
        width: 617px;
        @include cover;
        z-index: 3;
        transition: transform 0.8s;
        @include sm {
            padding-bottom: 270px;
            width: 250px; } }
    &__text {
        display: block;
        position: relative;
        top: -410px;
        left: 0;
        z-index: -1;
        color: $light-grey;
        @include sm {
            top: 0; } } }




.slick-slide {
    padding: 0 65px;
    width: auto !important;
    transition: transform 1.4s;
    @include sm {
        margin: 0 8px !important; } }

.slick-current {
    .slide__text {
        transform: translateY(-35px);
        transition: transform 1.4s;
        @include sm {
            transform: translateY(0); } } }
