@mixin col($column, $sum, $gap: 10px, $align: top, $first: false, $last: false) {
  flex: 0 0 percentage($column / $sum); }
//   font-size: 24px
//// display: inline-block
//   vertical-align: $align
//box-sizing: border-box
//padding-left: if($first, 0, $gap)
//padding-right: if($last, 0, $gap)

.out {
    height: 100%; }

.container-inner {
    max-width: 1430px;
    padding: 0 36px;
    margin: 0 auto;
    position: relative;
    @include lg {
        max-width: 1100px; }
    @include md {
        max-width: 800px; }
    @include sm {
        max-width: 100%;
        padding: 0 15px; } }


.row {
    display: flex;
    flex-wrap: wrap;
    margin-right: -15px;
    margin-left: -15px; }


.col1 {
    @include col(1,12); }
.col2 {
    @include col(2,12); }
.col3 {
    @include col(3,12); }
.col4 {
    @include col(4,12); }
.col5 {
    @include col(5,12); }
.col6 {
    @include col(6,12); }
.col7 {
    @include col(7,12); }
.col8 {
    @include col(8,12); }
.col9 {
    @include col(9,12); }
.col10 {
    @include col(10,12); }
.col11 {
    @include col(11,12); }
.col12 {
    @include col(12,12); }

[class*="col-"] {
    padding-right: 15px;
    padding-left: 15px; }

.col-24 {
    flex: 0 0 24%; }

.col-56 {
    flex: 0 0 55%; }

.col-31 {
    flex: 0 0 31%; }

.col-45 {
    flex: 0 0 45%; }

.col-46 {
    flex: 0 0 46%; }

.col-50 {
    flex: 0 0 50%; }

.col-76 {
    flex: 0 0 76%; }

[class*="col-"] {
    @include sm {
        flex: 0 0 100%; } }

.col {
    flex-basis: 0;
    flex-grow: 1;
    max-width: 100%;
    padding-right: 10px;
    padding-left: 10px; }

.d-lg-none {
    display: none;
    @include sm {
        display: block; } }

@include sm {
    .d-sm-none {
        display: none; } }
